	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>DUE DATE: Friday, October 26, 5 PM</strong></p>\n<h1>Introduction</h1>\n<p>For Sprint 2, you and your team are charged with creating a more usable version of the Battleship game.</p>\n<p>After the first version (Sprint 1) was released, we conducted focus group testing and found that users were complaining about the game interface being unusable. As a team, you have been tasked with creating a more usable interface for the next version.</p>\n<h1>Leaning Goals for this Sprint</h1>\n<p>This assignment will help you learn the following:</p>\n<ul>\n<li>Work with a team to design a user interface</li>\n<li>Communiciate your design of a user interface</li>\n<li>Divide tasks among team members</li>\n<li>Use Github Issue Tracking, Pull Requests, and Branches correctly</li>\n</ul>\n<h1>Tasks for this Sprint</h1>\n<p>For this sprint, each group needs to do the following:</p>\n<ol>\n<li>\n<p><strong>Prepare your team repo for Sprint 2.</strong> You may use your code from Sprint 1 if it runs and is feature complete, or you may copy the code form the <a href=\"https://github.com/cs361fall2018/sprint2\"><code>cs361fall2018/sprint2</code></a> repo into your team repo. We will be using the same <code>cs361fall2018/project-X</code> repository during Sprint 2, so if you choose to copy code from the <a href=\"https://github.com/cs361fall2018/sprint2\"><code>cs361fall2018/sprint2</code></a> repository you will need to research how to overwrite your previous code.</p>\n</li>\n<li>\n<p><strong>Decide on a target platform and layout for your application, as well as a description of the user you are targeting.</strong> This will inform your design. Designing for a vertical iPad layout is very different than a 16:9 laptop screen. The input method also will inform design decisions. The type of user you are targeting will also influence things. You should record these design details in a Wiki page on your team repository.</p>\n</li>\n<li>\n<p><strong>Create 3 different designs per team member on paper.</strong> Each team member should come up with 3 different designs for the application. These should be hand drawn on paper. One drawing per sheet of paper. Each drawing should be labeled with the authors name. You will turn in these drawings as a part of this assignment, by scanning and adding them to the Wiki pages on your team repository. Remember, <strong>you are each creating</strong> 3 <strong>different</strong> designs.</p>\n</li>\n<li>\n<p><strong>As a group choose the 3 best designs.</strong> As a group, you should vote the three best designs that you like from all the paper prototypes; if your in a team of 4 that means choosing from 12 designs, a team of 3 can choose from 9 designs. You need to mention why the team chose these 3 designs. Put this information (which was voted in and why) in the wiki page.</p>\n</li>\n<li>\n<p><strong>Merge the best ideas from top 3 designs into a single paper prototype.</strong> Work together as a group to create a new paper drawing that incorporates the best ideas from the 3 top choices. For this last one, you will need to turn in three different versions of the same design. This one \"best\" design should be drawn three times, once in each of the following states: </p>\n<ul>\n<li><strong>Regular:</strong> This is how it will look when the user is in the middle of playing a game.</li>\n<li><strong>Blank:</strong> This is how it will look when the user starts a new game.</li>\n<li><strong>Error:</strong> This is how it will look when the user tries to do an illigal ship placement or attack.</li>\n</ul>\n</li>\n<li>\n<p><strong>As a group analyze the usability of your prototype.</strong> On a separate Wiki page, describe your (sprint2) prototype according to Nielsen's 5 quality components of usability (<a href=\"https://www.nngroup.com/articles/usability-101-introduction-to-usability/\">link</a>). For each of the quality components, describe how your prototype addresses that component. If a component is not pertitent to your prototype, specify why that component is not applicable to your prototype.</p>\n</li>\n<li>\n<p><strong>Implement the code for your paper prototype as new interface for your application.</strong> This will include:</p>\n<ul>\n<li>Divide work up so that each team member has at least one task.</li>\n<li>Input tasks into Github Issue Tracker, and assign each team member at least one task.</li>\n<li>Each team member should write implementation code in a feature branch</li>\n<li>Each team member should have at least one pull request</li>\n<li>Each team member should make a constructive comment on another team members' pull request</li>\n</ul>\n</li>\n<li>\n<p>Tag the final version with <code>sprint-2</code>. Don’t forget to push the tags to GitHub, with <code>git push --tags</code>. <strong>This is the version we will grade!</strong></p>\n</li>\n<li>\n<p>(Optional) Complete <a href=\"https://goo.gl/forms/jzvDOPanWHOMLjBq1\">this survey</a> to give feedback regarding your team member's contribution to the sprint. Complete one survey for each team member you would like to give feedback for.</p>\n</li>\n</ol>","frontmatter":{"path":"/assignments/sprint2","title":"Sprint 2"}}},"pathContext":{}}

